import {Box, Button, RadioGroup, Typography, Radio, FormControlLabel} from '@mui/material'
import React, {FC, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux';
import {PickUpSelector} from "../../../store/selectors/selectors";
import {AppDispatch} from "../../../store/store";
import {useNavigate} from "react-router-dom";
import {setFromData} from "../../../store/slices/pick-up-slice";
import CustomAlert from "../../../components/alert";

const From: FC = () => {
    const [open, setOpen] = useState(false)
    const {from} = useSelector(PickUpSelector)
    const dispatch = useDispatch<AppDispatch>()
    const navigate = useNavigate()

    const handleBack = () => {
        navigate('/choose')
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const from = data.get('from') as string

        if (from) {
            dispatch(setFromData(from))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Забираем от:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-from"
                    name="radio-buttons-group"
                    defaultValue={from}
                >
                    <FormControlLabel name='from' value="подрядчик" control={<Radio/>} label="Подрядчик"/>
                    <FormControlLabel name='from' value="поставщик" control={<Radio/>} label="Поставщик"/>
                </RadioGroup>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button type="submit" variant="contained">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default From
