import {Box, Button, Typography, Grid, TextField, FormControlLabel, Snackbar, Alert} from '@mui/material'
import React, {FC, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux';
import {PickUpSelector} from "../../../store/selectors/selectors";
import {AppDispatch} from "../../../store/store";
import {decrementStep, setDateData,} from "../../../store/slices/pick-up-slice";
import {AdapterDayjs} from '@mui/x-date-pickers/AdapterDayjs';
import {LocalizationProvider} from '@mui/x-date-pickers/LocalizationProvider';
import {DatePicker} from '@mui/x-date-pickers/DatePicker';
import 'dayjs/locale/ru';
import Checkbox from "@mui/material/Checkbox";
import CustomAlert from "../../../components/alert";
import {Dayjs} from "dayjs";

const Type: FC = () => {
    const {
        pickUpDate,
        orderNumber,
        withContract: withContractStore,
        companyName
    } = useSelector(PickUpSelector)
    const dispatch = useDispatch<AppDispatch>()

    const [open, setOpen] = useState(false)
    const [date, setDate] = useState<Dayjs | null>(pickUpDate)
    const [withContract, setWithContract] = useState<boolean>(withContractStore)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const orderNumber = data.get('orderNumber') as string
        const companyName = data.get('companyName') as string

        if (date && orderNumber && (withContract && companyName || !withContract && !companyName)) {
            dispatch(setDateData({
                pickUpDate: date,
                orderNumber,
                withContract,
                companyName
            }))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Укажите следующие данные:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6}>
                        <LocalizationProvider
                            dateAdapter={AdapterDayjs}
                            adapterLocale='ru'
                        >
                            <DatePicker
                                label="Дата забора"
                                value={date}
                                onChange={e => setDate(e)}
                                renderInput={(params) => <TextField fullWidth {...params} />}
                            />
                        </LocalizationProvider>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            id="orderNumber"
                            name="orderNumber"
                            label="Номер заказа или счета"
                            fullWidth
                            autoComplete="orderNumber"
                            defaultValue={orderNumber}
                        />
                    </Grid>
                    <Grid item sm={12}>
                        <FormControlLabel
                            checked={withContract}
                            onChange={() => setWithContract(withContract => !withContract)}
                            control={<Checkbox/>}
                            label="Есть договоренность"
                            name="withContract"
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            disabled={!withContract}
                            required
                            id="companyName"
                            name="companyName"
                            label="Наименование ИП/ООО"
                            fullWidth
                            autoComplete="companyName"
                            defaultValue={companyName}
                        />
                    </Grid>
                </Grid>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Type
