import React, {useState} from 'react'
import {Box, Button, FormControlLabel, Radio, RadioGroup, Typography} from "@mui/material";
import {useDispatch, useSelector} from "react-redux";
import {AppDispatch} from "../../../store/store";
import {decrementStep, setPassData} from "../../../store/slices/delivery-slice";
import {DeliverySelector, PickUpSelector} from "../../../store/selectors/selectors";
import CustomAlert from "../../../components/alert";

const Pass = () => {
    const {needPass} = useSelector(DeliverySelector)
    const dispatch = useDispatch<AppDispatch>()

    const [open, setOpen] = useState(false)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const pass = data.get('pass') as string

        if (pass) {
            dispatch(setPassData({needPass: pass}))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Нужен ли пропуск:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-pass"
                    name="radio-buttons-group"
                    defaultValue={needPass}
                >
                    <FormControlLabel name="pass" value="нужен" control={<Radio/>} label="Пропуск нужен"/>
                    <FormControlLabel name="pass" value="не нужен" control={<Radio/>} label="Пропуск не нужен"/>
                </RadioGroup>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Pass
