import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {loginThunk, refreshThunk, registrationThunk} from "../thunks/auth-thunk";

interface InitialState {
    isAuth: boolean
    error: string | null
}

const initialState: InitialState = {
    isAuth: false,
    error: null
}

const AuthSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        setAuth(state, action: PayloadAction<boolean>) {
            state.isAuth = action.payload
            state.error = null
        },
        registration(state) {
            localStorage.setItem('auth', 'true')
            state.isAuth = true
        },
        clearError(state) {
            state.error = null
        }
    },
    extraReducers: (builder) => {
        builder.addCase(loginThunk.fulfilled, (state, action) => {
            if(action.payload.status === 200) {
                state.isAuth = true
                state.error = null

                localStorage.setItem('jwt', action.payload.data.jwt)
            } else {
                state.isAuth = false
                state.error = action.payload.data.message
            }
        })
        builder.addCase(registrationThunk.fulfilled, (state, action) => {
            if(action.payload.status === 200) {
                state.isAuth = true
                state.error = null

                localStorage.setItem('jwt', action.payload.data.jwt)
            } else {
                state.isAuth = false
                state.error = action.payload.data.message
            }
        })
        builder.addCase(refreshThunk.fulfilled, (state, action) => {
            if(action.payload.status === 200) {
                state.isAuth = true
                state.error = null

                localStorage.setItem('jwt', action.payload.data.jwt)
            } else {
                state.isAuth = false
                state.error = action.payload.data.message
            }
        })
    }
})

export const {clearError, registration} = AuthSlice.actions
export default AuthSlice.reducer
