import React, {FC, useState} from 'react'
import {Box, Button, FormControlLabel, Grid, Radio, RadioGroup, TextField, Typography} from "@mui/material";
import {useDispatch, useSelector} from "react-redux";
import {AppDispatch} from "../../../store/store";
import {decrementStep, setFromInfoData, setToInfoData} from "../../../store/slices/pick-up-slice";
import {LocalizationProvider, TimePicker} from "@mui/x-date-pickers";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import {PickUpSelector} from "../../../store/selectors/selectors";
import {Dayjs} from "dayjs";
import CustomAlert from "../../../components/alert";
import {useNavigate} from "react-router-dom";

const To: FC = () => {
    const {
        toLastName,
        toFirstName,
        toPhone,
        toExtraPhone,
        toAddress,
        toExtraAddress,
        toCompanyName,
        toPickUpTimeMin: toPickUpTimeMinStore,
        toPickUpTimeMax: toPickUpTimeMaxStore,
        toDocuments,
    } = useSelector(PickUpSelector)
    const dispatch = useDispatch<AppDispatch>()

    const [toPickUpTimeMin, setToPickUpTimeMin] = React.useState<Dayjs | null>(toPickUpTimeMinStore);
    const [toPickUpTimeMax, setToPickUpTimeMax] = React.useState<Dayjs | null>(toPickUpTimeMaxStore);
    const [open, setOpen] = useState(false)

    const [to, setTo] = useState('')

    const navigate = useNavigate()

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const lastName = data.get('lastName') as string
        const firstName = data.get('firstName') as string
        const phoneNumber = data.get('phoneNumber') as string
        const extraPhoneNumber = data.get('extraPhoneNumber') as string
        const address = data.get('address') as string
        const extraAddress = data.get('extraAddress') as string
        const documents = data.get('documents') as string
        const company = data.get('company') as string

        if (to === 'Офис' || lastName && firstName && phoneNumber && address && documents && company) {
            dispatch(setToInfoData({
                to,
                toLastName: lastName,
                toFirstName: firstName,
                toPhone: phoneNumber,
                toExtraPhone: extraPhoneNumber,
                toAddress: address,
                toExtraAddress: extraAddress,
                toCompanyName: company,
                toPickUpTimeMin,
                toPickUpTimeMax,
                toDocuments: documents
            }))
            navigate('/pick-up-check')
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Укажите адрес доставки:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-to"
                    name="radio-buttons-group"
                    value={to}
                    onChange={e => setTo(e.target.value)}
                >
                    <FormControlLabel value="Офис" control={<Radio/>} label="Офис"/>
                    <FormControlLabel value="Другое" control={<Radio/>} label="Другое"/>
                </RadioGroup>
                {to === 'Другое' &&
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                required
                                id="lastName"
                                name="lastName"
                                label="Фамилия"
                                fullWidth
                                autoComplete="family-name"
                                defaultValue={toLastName}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                required
                                id="firstName"
                                name="firstName"
                                label="Имя"
                                fullWidth
                                autoComplete="given-name"
                                defaultValue={toFirstName}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="phoneNumber"
                                name="phoneNumber"
                                label="Номер телефона"
                                fullWidth
                                autoComplete="tel"
                                type="phone"
                                defaultValue={toPhone}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="extraPhoneNumber"
                                name="extraPhoneNumber"
                                label="Доп. номер телефона"
                                fullWidth
                                autoComplete="tel"
                                type="phone"
                                defaultValue={toExtraPhone}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="address"
                                name="address"
                                label="Адрес"
                                fullWidth
                                autoComplete="street-address"
                                defaultValue={toAddress}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="extraAddress"
                                name="extraAddress"
                                label="Доп. адрес"
                                fullWidth
                                autoComplete="street-address"
                                defaultValue={toExtraAddress}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <LocalizationProvider
                                dateAdapter={AdapterDayjs}
                                adapterLocale='ru'
                            >
                                <TimePicker
                                    label="Время забора с"
                                    value={toPickUpTimeMin}
                                    onChange={(newValue) => setToPickUpTimeMin(newValue)}
                                    renderInput={(params) => <TextField fullWidth {...params} />}
                                />
                            </LocalizationProvider>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <LocalizationProvider
                                dateAdapter={AdapterDayjs}
                                adapterLocale='ru'
                            >
                                <TimePicker
                                    label="Время забора до"
                                    value={toPickUpTimeMax}
                                    onChange={(newValue) => setToPickUpTimeMax(newValue)}
                                    renderInput={(params) => <TextField fullWidth {...params} />}
                                />
                            </LocalizationProvider>
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="company"
                                name="company"
                                label="Название организации"
                                fullWidth
                                autoComplete="organization"
                                defaultValue={toCompanyName}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                required
                                id="documents"
                                name="documents"
                                label="Документы"
                                fullWidth
                                autoComplete="documents"
                                defaultValue={toDocuments}
                            />
                        </Grid>
                    </Grid>
                }
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Проверить
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default To
