import React, {FC, useEffect, useState} from 'react'
import {Box, Button, Container, CssBaseline, Grid, Paper, Typography} from "@mui/material";
import Header from "../../../components/header";
import {useSelector} from "react-redux";
import {PickUpSelector} from "../../../store/selectors/selectors";
import {useNavigate} from "react-router-dom";
import Copyright from "../../../components/copyright";
import dayjs from "dayjs";
import axios from "axios";
import CustomAlert from "../../../components/alert";

const PickUpCheck: FC = () => {
    const data = useSelector(PickUpSelector)
    const {
        step,
        from,
        type,
        pickUpDate,
        orderNumber,
        withContract,
        companyName,
        fromLastName,
        fromFirstName,
        fromPhone,
        fromExtraPhone,
        fromAddress,
        fromExtraAddress,
        fromCompanyName,
        fromPickUpTimeMin,
        fromPickUpTimeMax,
        fromDocuments,
        nature,
        natureDescription,
        needPass,
        call,
        to,
        toLastName,
        toFirstName,
        toPhone,
        toExtraPhone,
        toAddress,
        toExtraAddress,
        toCompanyName,
        toPickUpTimeMin,
        toPickUpTimeMax,
        toDocuments,
    }
        = useSelector(PickUpSelector)

    const navigate = useNavigate()
    const [success, setSuccess] = useState(false)
    const [error, setError] = useState(false)

    const sendData = () => {
        axios.post('https://закажи-доставку.рф/api-amocrm/pick-up', {...data,
            pickUpDate: dayjs(pickUpDate).format('DD.MM.YYYY'),
            fromPickUpTimeMin: dayjs(fromPickUpTimeMin).format('HH : mm'),
            fromPickUpTimeMax: dayjs(fromPickUpTimeMax).format('HH : mm'),
            toPickUpTimeMin: dayjs(toPickUpTimeMin).format('HH : mm'),
            toPickUpTimeMax: dayjs(toPickUpTimeMax).format('HH : mm'),
            withContract: withContract ? 'да' : 'нет'
        })
            .then(() => setSuccess(true))
            .catch(() => setError(true))
    }

    useEffect(() => {
        if (step < 7) {
            navigate('pick-up')
        }
    }, [])

    return (
        <>
            <CustomAlert isSuccess message={'Данные отправлены'} open={success} setOpen={setSuccess}/>
            <CustomAlert message={'Ошибка отправки'} open={error} setOpen={setError}/>
            <CssBaseline/>
            <Header/>
            <Container component="main" maxWidth="md" sx={{mb: 4}}>
                <Paper variant="outlined" sx={{my: {xs: 3, md: 6}, p: {xs: 2, md: 3}}}>
                    <Typography component="h1" variant="h4" align="center">
                        Проверка данных
                    </Typography>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                            <Typography variant="h6" align="center">
                                Откуда везем
                            </Typography>
                            <Typography variant="subtitle1">
                                Забираем от: {from}
                            </Typography>
                            <Typography variant="subtitle1">
                                Выберите вид услуги: {type}
                            </Typography>
                            <Typography variant="subtitle1">
                                Дата забора: {dayjs(pickUpDate).format('DD.MM.YYYY')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Номер заказа или счета: {orderNumber}
                            </Typography>
                            <Typography variant="subtitle1">
                                Есть договоренность: {withContract ? 'да' : 'нет'}
                            </Typography>
                            {withContract &&
                                <Typography variant="subtitle1">
                                    Наименование ИП/ООО: {companyName}
                                </Typography>
                            }
                            <Typography variant="subtitle1">
                                Фамилия: {fromLastName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Имя: {fromFirstName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Номер телефона: {fromPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                Доп. номер телефона: {fromExtraPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                Адрес: {fromAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                Доп. адрес: {fromExtraAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                Время забора с: {dayjs(fromPickUpTimeMin).format('HH : mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Время забора до: {dayjs(fromPickUpTimeMax).format('HH : mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Название организации: {fromCompanyName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Документы: {fromDocuments}
                            </Typography>
                            <Typography variant="subtitle1">
                                Укажите тип груза: {nature}
                            </Typography>
                            <Typography variant="subtitle1">
                                Описание груза: {natureDescription}
                            </Typography>
                            <Typography variant="subtitle1">
                                Нужен ли пропуск: {needPass}
                            </Typography>
                            <Typography variant="subtitle1">
                                Нужно ли созвониться заранее: {call}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Typography variant="h6" align="center">
                                Куда везем
                            </Typography>
                            <Typography variant="subtitle1">
                                Адрес доставки: {to}
                            </Typography>
                            {to === 'Другое' &&
                                <>
                                    <Typography variant="subtitle1">
                                        Фамилия: {toLastName}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Имя: {toFirstName}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Номер телефона: {toPhone}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Доп. номер телефона: {toExtraPhone}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Адрес: {toAddress}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Доп. адрес: {toExtraAddress}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Время забора с: {dayjs(toPickUpTimeMin).format('HH : mm')}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Время забора до: {dayjs(toPickUpTimeMax).format('HH : mm')}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Название организации: {toCompanyName}
                                    </Typography>
                                    <Typography variant="subtitle1">
                                        Документы: {toDocuments}
                                    </Typography>
                                </>
                            }
                        </Grid>
                    </Grid>
                    <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                        <Button>
                            Назад
                        </Button>
                        <Button variant="contained" onClick={sendData}>
                            Подтвердить
                        </Button>
                    </Box>
                </Paper>
                <Copyright/>
            </Container>
        </>
    )
}

export default PickUpCheck
