import React, {FC, useCallback} from 'react'
import {Container, CssBaseline, Paper, Step, StepLabel, Stepper, Typography} from "@mui/material";
import Copyright from '../../components/copyright'
import {useSelector} from 'react-redux';
import {DeliverySelector} from "../../store/selectors/selectors";
import To from "./steps/to";
import Header from "../../components/header";
import Type from "./steps/type";
import Date from "./steps/date";
import From from "./steps/from";
import Nature from "./steps/nature";
import Pass from "./steps/pass";
import Address from "./steps/address";
import Call from "./steps/call";

const Delivery: FC = () => {
    const {step} = useSelector(DeliverySelector)

    const getStepContent = useCallback(
        (step: number) => {
            switch (step) {
                case 0:
                    return <To/>
                case 1:
                    return <Type/>
                case 2:
                    return <Date/>
                case 3:
                    return <From/>
                case 4:
                    return <Nature/>
                case 5:
                    return <Address/>
                case 6:
                    return <Pass/>
                case 7:
                    return <Call/>
            }
        }, [])

    return (
        <>
            <CssBaseline/>
            <Header/>
            <Container component="main" maxWidth="sm" sx={{mb: 4}}>
                <Paper variant="outlined" sx={{my: {xs: 3, md: 6}, p: {xs: 2, md: 3}}}>
                    <Typography component="h1" variant="h4" align="center">
                        Доставка груза
                    </Typography>
                    <Stepper activeStep={step} alternativeLabel sx={{pt: 3, pb: 5}}>
                        {Array(8).fill('').map((el, i) => (
                            <Step key={i}>
                                <StepLabel/>
                            </Step>
                        ))}
                    </Stepper>
                    <>
                        {getStepContent(step)}
                    </>
                </Paper>
                <Copyright/>
            </Container>
        </>
    )
}

export default Delivery
