import React, {FC, useState} from 'react'
import {
    Box,
    Button,
    FormControlLabel,
    Radio,
    RadioGroup,
    TextField,
    Typography
} from "@mui/material";
import {decrementStep, setNatureData} from "../../../store/slices/pick-up-slice";
import {useDispatch, useSelector} from "react-redux";
import {AppDispatch} from "../../../store/store";
import {PickUpSelector} from "../../../store/selectors/selectors";
import CustomAlert from "../../../components/alert";

const Nature: FC = () => {
    const {nature, natureDescription} = useSelector(PickUpSelector)
    const dispatch = useDispatch<AppDispatch>()

    const [open, setOpen] = useState(false)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const nature = data.get('nature') as string
        const description = data.get('description') as string

        if (nature && description) {
            dispatch(setNatureData({nature, natureDescription: description}))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Укажите тип груза:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-nature"
                    name="radio-buttons-group"
                    defaultValue={nature}
                >
                    <FormControlLabel name="nature" value="коробка" control={<Radio/>} label="Коробка"/>
                    <FormControlLabel name="nature" value="пачка" control={<Radio/>} label="Пачка"/>
                    <FormControlLabel name="nature" value="другое" control={<Radio/>} label="Другое"/>
                </RadioGroup>
                <TextField
                    defaultValue={natureDescription}
                    name="description"
                    required
                    id="outlined-multiline-flexible"
                    label="Описание"
                    multiline
                    maxRows={4}
                    placeholder="ВАЖНО: После выбора Коробка и Пачка, необходимо написать количество и вес. При выборе ДРУГОЕ опишите характер груза (например: Баннер длинной 2,30м; Паллет 400кг и т.д.)"
                    fullWidth
                />
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Nature
