import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {Dayjs} from "dayjs";

interface State {
    step: number
    from: string,
    type: string
    pickUpDate: null | Dayjs
    orderNumber: string
    withContract: boolean
    companyName: string
    fromLastName: string
    fromFirstName: string
    fromPhone: string
    fromExtraPhone: string
    fromAddress: string
    fromExtraAddress: string
    fromCompanyName: string
    fromPickUpTimeMin: null | Dayjs
    fromPickUpTimeMax: null | Dayjs
    fromDocuments: string
    nature: string
    natureDescription: string
    needPass: string
    call: string

    to: string
    toLastName: string
    toFirstName: string
    toPhone: string
    toExtraPhone: string
    toAddress: string
    toExtraAddress: string
    toCompanyName: string
    toPickUpTimeMin: null | Dayjs
    toPickUpTimeMax: null | Dayjs
    toDocuments: string
}

const initialState: State = {
    step: 0,
    from: '',
    type: '',
    pickUpDate: null,
    orderNumber: '',
    withContract: false,
    companyName: '',
    fromLastName: '',
    fromFirstName: '',
    fromPhone: '',
    fromExtraPhone: '',
    fromAddress: '',
    fromExtraAddress: '',
    fromCompanyName: '',
    fromPickUpTimeMin: null,
    fromPickUpTimeMax: null,
    fromDocuments: '',
    nature: '',
    natureDescription: '',
    needPass: '',
    call: '',

    to: '',
    toLastName: '',
    toFirstName: '',
    toPhone: '',
    toExtraPhone: '',
    toAddress: '',
    toExtraAddress: '',
    toCompanyName: '',
    toPickUpTimeMin: null,
    toPickUpTimeMax: null,
    toDocuments: '',
}

const PickUpSlice = createSlice({
    name: 'receipt',
    initialState,
    reducers: {
        incrementStep(state) {
            ++state.step
        },
        decrementStep(state) {
            --state.step
        },
        setFromData(state, action: PayloadAction<string>) {
            ++state.step
            state.from = action.payload
        },
        setTypeData(state, action: PayloadAction<string>) {
            ++state.step
            state.type = action.payload
        },
        setDateData(state, action: PayloadAction<{
            pickUpDate: Dayjs | null,
            orderNumber: string,
            withContract: boolean,
            companyName: string
        }>) {
            ++state.step
            state.pickUpDate = action.payload.pickUpDate
            state.orderNumber = action.payload.orderNumber
            state.withContract = action.payload.withContract
            state.companyName = action.payload.companyName
        },
        setFromInfoData(state, action: PayloadAction<{
            fromLastName: string
            fromFirstName: string
            fromPhone: string
            fromExtraPhone: string
            fromAddress: string
            fromExtraAddress: string
            fromCompanyName: string
            fromPickUpTimeMin: null | Dayjs
            fromPickUpTimeMax: null | Dayjs
            fromDocuments: string
        }>) {
            ++state.step
            state.fromLastName = action.payload.fromLastName
            state.fromFirstName = action.payload.fromFirstName
            state.fromPhone = action.payload.fromPhone
            state.fromExtraPhone = action.payload.fromExtraPhone
            state.fromAddress = action.payload.fromAddress
            state.fromExtraAddress = action.payload.fromExtraAddress
            state.fromCompanyName = action.payload.fromCompanyName
            state.fromPickUpTimeMin = action.payload.fromPickUpTimeMin
            state.fromPickUpTimeMax = action.payload.fromPickUpTimeMax
            state.fromDocuments = action.payload.fromDocuments
        },
        setNatureData(state, action: PayloadAction<{
            nature: string
            natureDescription: string
        }>) {
            ++state.step
            state.nature = action.payload.nature
            state.natureDescription = action.payload.natureDescription
        },
        setPassData(state, action: PayloadAction<{ needPass: string }>) {
            ++state.step
            state.needPass = action.payload.needPass
        },
        setCallData(state, action: PayloadAction<{ call: string }>) {
            ++state.step
            state.call = action.payload.call
        },
        setToInfoData(state, action: PayloadAction<{
            to: string
            toLastName: string
            toFirstName: string
            toPhone: string
            toExtraPhone: string
            toAddress: string
            toExtraAddress: string
            toCompanyName: string
            toPickUpTimeMin: null | Dayjs
            toPickUpTimeMax: null | Dayjs
            toDocuments: string
        }>) {
            state.to = action.payload.to
            state.toLastName = action.payload.toLastName
            state.toFirstName = action.payload.toFirstName
            state.toPhone = action.payload.toPhone
            state.toExtraPhone = action.payload.toExtraPhone
            state.toAddress = action.payload.toAddress
            state.toExtraAddress = action.payload.toExtraAddress
            state.toCompanyName = action.payload.toCompanyName
            state.toPickUpTimeMin = action.payload.toPickUpTimeMin
            state.toPickUpTimeMax = action.payload.toPickUpTimeMax
            state.toDocuments = action.payload.toDocuments
        }
    }
})

export const {
    incrementStep,
    decrementStep,
    setFromData,
    setTypeData,
    setDateData,
    setFromInfoData,
    setNatureData,
    setPassData,
    setCallData,
    setToInfoData
} = PickUpSlice.actions
export default PickUpSlice.reducer
