import {Box, Button, RadioGroup, Typography, Radio, FormControlLabel} from '@mui/material'
import React, {FC, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux';
import {DeliverySelector} from "../../../store/selectors/selectors";
import {AppDispatch} from "../../../store/store";
import {useNavigate} from "react-router-dom";
import CustomAlert from "../../../components/alert";
import {setToData} from "../../../store/slices/delivery-slice";

const From: FC = () => {
    const [open, setOpen] = useState(false)
    const {to} = useSelector(DeliverySelector)
    const dispatch = useDispatch<AppDispatch>()
    const navigate = useNavigate()

    const handleBack = () => {
        navigate('/choose')
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const to = data.get('to') as string

        if (to) {
            dispatch(setToData(to))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Кому везем:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-from"
                    name="radio-buttons-group"
                    defaultValue={to}
                >
                    <FormControlLabel name='to' value="клиент" control={<Radio/>} label="Клиент"/>
                    <FormControlLabel name='to' value="подрядчик" control={<Radio/>} label="Подрядчик"/>
                </RadioGroup>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button type="submit" variant="contained">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default From
