import React, {FC, useState} from 'react'
import {Box, Button, FormControlLabel, Radio, RadioGroup, Typography} from "@mui/material";
import {useDispatch, useSelector} from "react-redux";
import {AppDispatch} from "../../../store/store";
import {decrementStep, setCallData} from "../../../store/slices/delivery-slice";
import {DeliverySelector} from "../../../store/selectors/selectors";
import CustomAlert from "../../../components/alert";
import {useNavigate} from "react-router-dom";

const Call: FC = () => {
    const {call} = useSelector(DeliverySelector)
    const dispatch = useDispatch<AppDispatch>()
    const navigation = useNavigate()

    const [open, setOpen] = useState(false)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const call = data.get('call') as string

        if (call) {
            dispatch(setCallData({call}))
            navigation('/delivery-check')
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Нужно ли созвониться заранее:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <RadioGroup
                    aria-labelledby="pick-up-call"
                    name="radio-buttons-group"
                    defaultValue={call}
                >
                    <FormControlLabel name="call" value="15 минут" control={<Radio/>} label="Созвониться за 15 минут"/>
                    <FormControlLabel name="call" value="30 минут" control={<Radio/>} label="Созвониться за 30 минут"/>
                    <FormControlLabel name="call" value="1 час" control={<Radio/>} label="Созвониться за час"/>
                    <FormControlLabel name="call" value="3 часа" control={<Radio/>} label="Созвониться за 3 часа"/>
                    <FormControlLabel name="call" value="не нужно" control={<Radio/>} label="Созвониваться не нужно"/>
                </RadioGroup>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Call
