import React, {FC} from 'react'
import {AppBar, Toolbar, Typography} from "@mui/material";

const Header: FC = () => {

    return (
        <AppBar
            position="absolute"
            color="default"
            elevation={0}
            sx={{
                position: 'relative',
                borderBottom: (t) => `1px solid ${t.palette.divider}`,
            }}
        >
            <Toolbar>
                <Typography variant="h6" color="inherit" noWrap>
                    закажи-доставку.рф
                </Typography>
            </Toolbar>
        </AppBar>
    )
}

export default Header
