import {createAsyncThunk} from "@reduxjs/toolkit";
import axios from 'axios'
import {User} from "../../interfaces/interfaces";

const API_URL = 'https://закажи-доставку.рф/api'

export const loginThunk = createAsyncThunk(
    'auth/login',
    async (data: { email: string, password: string }) => {
        const res = await axios.post(API_URL + '/auth/login', data, {validateStatus: null})

        return {
            status: res.status,
            data: res.data
        }
    }
)

export const registrationThunk = createAsyncThunk(
    'auth/registration',
    async (data: User) => {
        const res = await axios.post(API_URL + '/auth/registration', data, {validateStatus: null})

        return {
            status: res.status,
            data: res.data
        }
    }
)

export const refreshThunk = createAsyncThunk(
    'auth/refresh',
    async (data: { jwt: string }) => {
        const res = await axios.post(API_URL + '/auth/refresh', data, {validateStatus: null})

        return {
            status: res.status,
            data: res.data
        }
    }
)
