import React, {FC, useState} from 'react'
import {useDispatch, useSelector} from "react-redux";
import {DeliverySelector} from "../../../store/selectors/selectors";
import {AppDispatch} from "../../../store/store";
import {Dayjs} from "dayjs";
import CustomAlert from "../../../components/alert";
import {Box, Button, Grid, TextField, Typography} from "@mui/material";
import {LocalizationProvider, TimePicker} from "@mui/x-date-pickers";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import {decrementStep, setAddressData} from "../../../store/slices/delivery-slice";

const Address: FC = () => {
    const {
        toLastName,
        toFirstName,
        toPhone,
        toExtraPhone,
        toAddress,
        toExtraAddress,
        toCompanyName,
        toPickUpTimeMin: toPickUpTimeMinStore,
        toPickUpTimeMax: toPickUpTimeMaxStore,
        toDocuments,
    } = useSelector(DeliverySelector)
    const dispatch = useDispatch<AppDispatch>()

    const [toPickUpTimeMin, setFromPickUpTimeMin] = React.useState<Dayjs | null>(toPickUpTimeMinStore);
    const [toPickUpTimeMax, setFromPickUpTimeMax] = React.useState<Dayjs | null>(toPickUpTimeMaxStore);
    const [open, setOpen] = useState(false)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const lastName = data.get('lastName') as string
        const firstName = data.get('firstName') as string
        const phoneNumber = data.get('phoneNumber') as string
        const extraPhoneNumber = data.get('extraPhoneNumber') as string
        const address = data.get('address') as string
        const extraAddress = data.get('extraAddress') as string
        const documents = data.get('documents') as string
        const company = data.get('company') as string

        if (lastName && firstName && phoneNumber && address && documents && company) {
            dispatch(setAddressData({
                toLastName: lastName,
                toFirstName: firstName,
                toPhone: phoneNumber,
                toExtraPhone: extraPhoneNumber,
                toAddress: address,
                toExtraAddress: extraAddress,
                toCompanyName: company,
                toPickUpTimeMin,
                toPickUpTimeMax,
                toDocuments: documents
            }))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Укажите данные от кого забирать:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            id="lastName"
                            name="lastName"
                            label="Фамилия"
                            fullWidth
                            autoComplete="family-name"
                            defaultValue={toLastName}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            id="firstName"
                            name="firstName"
                            label="Имя"
                            fullWidth
                            autoComplete="given-name"
                            defaultValue={toFirstName}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="phoneNumber"
                            name="phoneNumber"
                            label="Номер телефона"
                            fullWidth
                            autoComplete="tel"
                            type="phone"
                            defaultValue={toPhone}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            id="extraPhoneNumber"
                            name="extraPhoneNumber"
                            label="Доп. номер телефона"
                            fullWidth
                            autoComplete="tel"
                            type="phone"
                            defaultValue={toExtraPhone}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="address"
                            name="address"
                            label="Адрес"
                            fullWidth
                            autoComplete="street-address"
                            defaultValue={toAddress}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            id="extraAddress"
                            name="extraAddress"
                            label="Доп. адрес"
                            fullWidth
                            autoComplete="street-address"
                            defaultValue={toExtraAddress}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <LocalizationProvider
                            dateAdapter={AdapterDayjs}
                            adapterLocale='ru'
                        >
                            <TimePicker
                                label="Время забора с"
                                value={toPickUpTimeMin}
                                onChange={(newValue) => setFromPickUpTimeMin(newValue)}
                                renderInput={(params) => <TextField fullWidth {...params} />}
                            />
                        </LocalizationProvider>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <LocalizationProvider
                            dateAdapter={AdapterDayjs}
                            adapterLocale='ru'
                        >
                            <TimePicker
                                label="Время забора до"
                                value={toPickUpTimeMax}
                                onChange={(newValue) => setFromPickUpTimeMax(newValue)}
                                renderInput={(params) => <TextField fullWidth {...params} />}
                            />
                        </LocalizationProvider>
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="company"
                            name="company"
                            label="Название организации"
                            fullWidth
                            autoComplete="organization"
                            defaultValue={toCompanyName}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="documents"
                            name="documents"
                            label="Документы"
                            fullWidth
                            autoComplete="documents"
                            defaultValue={toDocuments}
                        />
                    </Grid>
                </Grid>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Address
