import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Copyright from "../../components/copyright";
import {FC, useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import CustomAlert from "../../components/alert";
import {Link} from '@mui/material';
import {useNavigate} from "react-router-dom";
import {registrationThunk} from "../../store/thunks/auth-thunk";
import {AuthSelector} from "../../store/selectors/selectors";
import {clearError} from "../../store/slices/auth-slice";


const SignUp: FC = () => {
    const {error} = useSelector(AuthSelector)
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [open, setOpen] = useState(false)
    const [accept, setAccept] = useState(false)

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        if (
            data.get('email') &&
            data.get('password') &&
            data.get('firstName') &&
            data.get('lastName') &&
            data.get('company') &&
            accept
        ) {
            // @ts-ignore
            dispatch(registrationThunk({
                email: data.get('email') as string,
                password: data.get('password') as string,
                firstName: data.get('firstName') as string,
                lastName: data.get('lastName') as string,
                company: data.get('company') as string,
            }))
        } else {
            setOpen(true)
        }
    };

    useEffect(() => {
        if (error) {
            setOpen(true)
        }
    }, [error])

    useEffect(() => {
        if (!open) {
            dispatch(clearError())
        }
    }, [open])

    return (
        <>
            {open && <CustomAlert message={error} open={open} setOpen={setOpen}/>}
            <Container component="main" maxWidth="xs">
                <CssBaseline/>
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{m: 1, bgcolor: 'secondary.main'}}>
                        <LockOutlinedIcon/>
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Регистрация
                    </Typography>
                    <Box component="form" noValidate onSubmit={handleSubmit} sx={{mt: 3}}>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    autoComplete="given-name"
                                    name="firstName"
                                    required
                                    fullWidth
                                    id="firstName"
                                    label="Имя"
                                    autoFocus
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    required
                                    fullWidth
                                    id="lastName"
                                    label="Фамилия"
                                    name="lastName"
                                    autoComplete="family-name"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    id="company"
                                    label="Название компании"
                                    name="company"
                                    autoComplete="company"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email"
                                    name="email"
                                    autoComplete="email"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    required
                                    fullWidth
                                    name="password"
                                    label="Пароль"
                                    type="password"
                                    id="password"
                                    autoComplete="new-password"
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    checked={accept}
                                    onChange={() => setAccept(accept => !accept)}
                                    control={<Checkbox value="allowExtraEmails" color="primary"/>}
                                    label="Я соглашаюсь с условиями обработки персональных данных"
                                />
                            </Grid>
                        </Grid>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{mt: 3, mb: 2}}
                        >
                            Зарегистрироваться
                        </Button>
                        <Grid container justifyContent="center">
                            <Grid item>
                                <Link onClick={() => navigate('/sign-in')} variant="body2">
                                    Уже есть аккаунт? Вход
                                </Link>
                            </Grid>
                        </Grid>
                    </Box>
                </Box>
                <Copyright sx={{mt: 5}}/>
            </Container>
        </>
    );
}

export default SignUp
